<template >
  <div>
    <br/>
    <div>
      选择一个功能：
      <el-select v-model="value" placeholder="请选择" @change="getContent">
        <el-option
          v-for="item in data"
          :key="item.name"
          :label="item.name"
          :value="item.name">
        </el-option>
      </el-select>
    </div>
    <br/>
    <el-input type="textarea" :rows="10" v-model="content"style="width: 600px"></el-input>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        value: '',
        data:[{
          name: '__Random',
          content:'使用方式：${__Random(param1,param2,param3)}，返回一个随机数字。\n' +
            '参数说明：param1最小值，param2最大值,param3(可选)保存的变量名。\n' +
            '举例: ${__Random(1,100,Number)}，获取一个1~100间的随机数字，保存为变量Number。${__Random(1,100)}，仅获取一个1~100间的随机数字。'
        },{
          name: '__time',
          content: '使用方式：${__time(param1,param2)}，返回时间。\n' +
            '参数说明：param1 时间格式，param2(可选)保存的变量名\n' +
            '举例：\n' +
            '${__time(,)}  获取当前时间毫秒 \n'+
            '${__time(/1000,TIME)}  获取当前时间秒，并保存为变量TIME \n' +
            '${__time(yyyy-MM-dd,)}  获取yyyy-MM-dd格式时间 \n' +
            '${__time(yyyy-MM-dd HH:mm:ss,DATE)}   获取yyyy-MM-dd HH:mm:ss格式时间，并保存为变量DATE'
        }],
        content:'',
      }
    },
    methods: {
      getContent(){
        this.data.forEach((item)=>{
          if (item.name === this.value){
            this.content = item.content
          }
        })
      }
    }

  }
</script>

<style scoped>
</style>



