<template>
  <div>
    <el-card>
      <el-input v-model="search.caseName" placeholder="接口名称" style="width: 150px"></el-input>
      <el-select v-model="search.systemName" placeholder="系 统" style="width: 150px" clearable filterable>
        <el-option v-for="item in search.systemNameList" :key="item.categoryName" :label="item.categoryName" :value="item.categoryName"/>
      </el-select>
      <el-select v-model="search.createrCode" placeholder="创建人" style="width: 150px" clearable filterable>
        <el-option v-for="item in search.createrList" :key="item.createrCode" :label="item.createrName" :value="item.createrCode">
          <span style="float: left">{{ item.createrName }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.createrCode }}</span>
        </el-option>
      </el-select>
      <el-button  type="primary" style="float: right" @click="searchCaseList">筛 选</el-button>
    </el-card>
    <br/>
    <el-card>
      <el-row>
        <el-button type="primary" size="small" @click="createCase">新 增</el-button>
        <el-button type="primary" size="small" @click="copyCase">复 制</el-button>
        <el-button type="primary" size="small" @click="confirmDeleteCase">删 除</el-button>
        <el-button type="success" size="small" :loading="table.batchExcuteButtonLoding" @click="batchExcuteSamples">执 行<i class="el-icon-caret-right el-icon--right"></i></el-button>
        <el-pagination background
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="table.page"
                       :page-size="table.pageSize"
                       :page-sizes="[10, 20, 50, 100]"
                       :total="table.total" layout="total, sizes, prev, pager, next" style="float: right">
        </el-pagination>
        <el-dialog :visible.sync="table.deleteConfirmDialogVisibel" width="400px">
          <span>确认删除？</span>
          <span slot="footer" class="dialog-footer">
              <el-button @click="table.deleteConfirmDialogVisibel = false">取 消</el-button>
              <el-button type="primary" @click="deleteCase">确 定</el-button>
            </span>
        </el-dialog>
      </el-row>
      <br/>
      <el-row>
        <el-table ref="multipleTable" :data="table.tableData" tooltip-effect="dark" @selection-change="tableSelectionChange" stripe>
          <el-table-column type="selection"/>
          <el-table-column prop="name" label="接口名称">
            <template slot-scope="scope">
              <el-button type="text" v-model="scope.row.caseName" @click="editCase(scope.row)">{{scope.row.caseName}}</el-button>
            </template>
          </el-table-column>
          <el-table-column label="版本" align="center">
            <template slot-scope="scope">
              <el-tag size="mini">{{scope.row.version}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="systemName" label="系 统" align="center" sortable/>
          <el-table-column prop="createrName" label="创建人" align="center" sortable/>
          <el-table-column prop="updaterName" label="最后更新人" align="center" sortable/>
          <el-table-column prop="updateDate" label="最后更新时间" align="center" sortable/>
          <el-table-column prop="description" label="备注"/>
          <el-table-column prop="isSuccess" label="状态" sortable>
            <template slot-scope="scope">
              <el-tag :type="scope.row.status === true ? 'success':'danger'">{{getCaseStatus(scope.row.status)}}</el-tag>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-card>
    <el-dialog :visible.sync="dialog.dialogVisible" :close-on-click-modal=false width="1000px">
      <el-row v-show="dialog.userInfoIsShow">
        <el-col :span="3">
          <span>创建人: {{dialog.caseInfo.createrName}}</span>
        </el-col>
        <el-col :span="3" :offset="1">
          <span>最后更新: {{dialog.caseInfo.updaterName}}</span>
        </el-col>
        <el-col :span="5" :offset="1">
          <span>最后更新时间: {{dialog.caseInfo.updateDate}}</span>
        </el-col>
        <el-col :span="3" :offset="1">
          <span>状态: {{getCaseStatus(dialog.caseInfo.status)}}</span>
        </el-col>
      </el-row>
      <br/>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>基础配置</span>
        </div>
        <el-form label-position="right" label-width="100px">
          <el-form-item label="分类名称：" prop="systemName">
            <el-select v-model="dialog.caseInfo.systemName"  style="width: 500px">
              <el-option v-for="item in search.systemNameList" :key="item.categoryName" :label="item.categoryName" :value="item.categoryName"/>
            </el-select>
          </el-form-item>
          <el-form-item label="接口名称：">
            <el-input v-model="dialog.caseInfo.caseName" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="ZK地址：">
            <el-input v-model="dialog.caseInfo.zkAddress" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="服务名：">
            <el-input v-model="dialog.caseInfo.serviceName" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="GROUP：">
            <el-input v-model="dialog.caseInfo.groupName" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="版本：">
            <el-input v-model="dialog.caseInfo.version" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="入参：">
            <el-input v-model="dialog.caseInfo.params" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="函数名：">
            <el-input v-model="dialog.caseInfo.functionName" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="类名：">
            <el-input v-model="dialog.caseInfo.className" style="width: 500px"></el-input>
          </el-form-item>
          <el-form-item label="备注信息：">
            <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 4}" placeholder="备注" v-model="dialog.caseInfo.description" style="width: 500px"/>
          </el-form-item>
        </el-form>
      </el-card>
      <br/>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>返回结果</span>
        </div>
        <el-collapse v-model="dialog.responseParams.activeCollapse">
          <el-collapse-item title="保存变量" name="variable">
            <el-button type="primary" size="mini" @click="addVarialbe">新 增</el-button>
            <el-form>
              <el-form-item v-for="(item,index) in dialog.caseInfo.variableList" :key="index" style="margin-bottom: 0px">
                <el-select v-model="item.extractMethod" placeholder="请选择取值方式" size="small" style="width: 150px">
                  <el-option v-for="variableType in dialog.responseParams.variableTypeList" :key="variableType.value" :label="variableType.label" :value="variableType.value"/>
                </el-select>
                <el-input style="width: 250px" size="small" v-model="item.extractRule" placeholder="提取路径"></el-input>
                <el-tooltip class="item" effect="dark" content="为保持变量名规范，请输入大写、下划线，类似于VARIABLE_TYPE,引用时使用${VARIABLE_TYPE}" placement="top-end">
                  <el-input style="width: 100px" size="small" v-model="item.variableName" placeholder="变量名"></el-input>
                </el-tooltip>
                <el-button type="text" @click="deleteVarialbe(index)"><i class="el-icon-delete"></i> </el-button>
              </el-form-item>
            </el-form>
          </el-collapse-item>
          <el-collapse-item title="校验" name="expected">
            <el-button type="primary" size="mini" @click="addExpected">新 增</el-button>
            <el-form>
              <el-form-item v-for="(item,index) in dialog.caseInfo.expectedList" :key="index" style="margin-bottom: 0px">
                <el-select v-model="item.extractMethod" placeholder="获取方式" size="small" style="width: 120px">
                  <el-option v-for="type in dialog.responseParams.getValueTypeList" :key="type.value" :label="type.label" :value="type.value"/>
                </el-select>
                <el-input style="width: 250px" size="small" v-model="item.extractRule" placeholder="提取路径/规则"></el-input>
                <el-select v-model="item.compareType" placeholder="校验方式" size="small" style="width: 100px">
                  <el-option v-for="type in dialog.responseParams.expectedTypeList" :key="type.value" :label="type.label" :value="type.value"/>
                </el-select>
                <el-input style="width: 200px" size="small" v-model="item.expectedValue" placeholder="预期值"></el-input>
                <el-button type="text" @click="deleteExpected(index)"><i class="el-icon-delete"></i> </el-button>
              </el-form-item>
            </el-form>
          </el-collapse-item>
          <el-collapse-item title="调试" name="debug" v-show="dialog.responseParams.canDebug">
            <el-button :type="dialog.responseParams.excuteButtonStauts" size="small" @click="excuteSample" :loading="dialog.responseParams.loading">执 行<i class="el-icon-caret-right el-icon--right"></i></el-button>
            <br/>
            <br/>
            <el-input v-model="dialog.caseInfo.result" type="textarea" :autosize="{ minRows: 6, maxRows: 10}" placeholder="返回结果"/>
          </el-collapse-item>
        </el-collapse>
      </el-card>
      <br/>
      <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="saveCase">保 存</el-button>
          <el-button @click="dialog.dialogVisible = false">取 消</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
    export default {
        name: "DubboCaseManage.vue",
      data() {
        return {
          operationType: 0,  // 操作方式：1.新增 2.修改 3.复制
          search: {
            caseName: '',
            systemName: '',
            systemNameList: [],
            createrCode: '',
            createrList: [],
          },
          table:{
            deleteConfirmDialogVisibel: false,
            batchExcuteButtonLoding: false,
            page: 1,
            pageSize: 10,
            total: 0,
            tableData: [],
            multipleSelection: [],
            ids: [],
          },
          dialog: {
            dialogVisible: false,
            userInfoIsShow: false,
            defaultCaseInfo: {
              createrName: "",
              createrCode: '',
              createDate: "",
              updaterName: "",
              updaterCode: '',
              updateDate: "",
              systemName: "",
              caseName: "",
              apiName: "",
              zkAddress: "",
              serviceName: "",
              groupName: "",
              version: "",
              params: "",
              functionName: "",
              className: "",
              description: "",
              variableList: [],
              expectedList: [],
              status: false,
              result: '',
            },
            caseInfo: {},
            responseParams: {
              activeCollapse: 'debug',
              variable: [],
              variableTypeList: [{
                label: 'JsonPath提取',
                value: 1,
              },{
                label: '正则表达式提取',
                value: 2,
              }],
              expected: [],
              getValueType: '',
              getValueTypeList: [{
                label: 'JsonPath',
                value: 1,
              },{
                label: '正则表达式',
                value: 2,
              }],
              expectedType: '',
              expectedTypeList: [{
                label: 'equals',
                value: 1,
              },{
                label: 'contains',
                value: 2,
              }],
              canDebug: false,
              loading: false,
              excuteButtonStauts: 'success',
            },
          },

        }
      },
      mounted(){
        this.getSystemNameList();
        this.getCreaterList();
        this.searchCaseList();
      },
      watch: {
        operationType: function (val) {
          // 根据操作方式(新增、修改、删除)不同，控制各个组件是否显示
          if (val === 1){
            this.dialog.userInfoIsShow = false;
            this.dialog.responseParams.canDebug = false;
          } else if (val === 2){
            this.dialog.userInfoIsShow = true;
            this.dialog.responseParams.canDebug = true;
          }
        }
      },
      methods:{
        // 获取分类列表
        getSystemNameList(){
          this.$axios({
            method:"get",
            url: "/category/list",
          }).then(
            res=>{
              this.search.systemNameList = res.data.result;
            },err =>{
            });
        },
        // 获取创建人列表
        getCreaterList(){
          this.$axios({
            method:"get",
            url: "/dubbo/creater",
          }).then(
            res=>{
              this.search.createrList = res.data.result;
            },err =>{
            });
        },
        // 搜索符合条件的所有testCase
        searchCaseList(){
          this.$axios({
            method:"post",
            url: "/dubbo/search",
            headers:{
              'Content-type': 'application/json'
            },
            data:{
              "page": this.table.page,
              "pageSize":this.table.pageSize,
              "dubboCase": this.search
            }
          }).then(
            res=>{
              this.table.tableData = res.data.result.list;
              this.table.total = res.data.result.total;
            },err =>{
            });
        },
        // 创建case
        createCase(){
          this.operationType = 1;
          this.dialog.dialogVisible = true;
          this.dialog.caseInfo = {...this.dialog.defaultCaseInfo};
        },
        // 复制case
        copyCase(){
          this.operationType = 3;
          if (this.table.multipleSelection.length === 1){
            this.dialog.caseInfo = this.table.multipleSelection[0];
            this.dialog.caseInfo.createDate = '';
            this.dialog.caseInfo.updateDate = '';
            this.saveCase();
          } else {
            this.$message.warning("使用复制时必须且只能选中一条case！");
          }
        },
        saveCase(){
          let requestUrl = '';
          if (this.operationType === 1 || this.operationType === 3) {
            requestUrl = "/dubbo/create";
          } else if (this.operationType === 2){
            requestUrl = "/dubbo/update";
          }
          this.$axios({
            method:"post",
            url: requestUrl,
            headers:{
              'Content-type': 'application/json'
            },
            data:this.dialog.caseInfo,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.$message.success(res.data.result);
                this.dialog.dialogVisible = false;
                this.searchCaseList();
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
        confirmDeleteCase(){
          if (this.table.multipleSelection.length > 0){
            this.table.deleteConfirmDialogVisibel = true;
          } else {
            this.$message.warning("请选择至少一条case！");
          }
        },
        batchExcuteSamples(){
          this.table.batchExcuteButtonLoding = true;
          this.$axios({
            method:"post",
            url: "/dubbo/excute",
            headers:{
              'Content-type': 'application/json'
            },
            data:this.table.ids,
          }).then(
            res=>{
              this.$message.success("批量执行完成！");
              this.table.batchExcuteButtonLoding = false;
              this.searchCaseList();
            },err =>{
              this.table.batchExcuteButtonLoding = false;
            });
        },
        handleSizeChange(val){
          this.table.pageSize = val;
          this.searchCaseList();
        },
        handleCurrentChange(val){
          this.table.page = val;
          this.searchCaseList();
        },
        deleteCase(){
          this.$axios({
            method:"post",
            url: '/dubbo/delete',
            headers:{
              'Content-type': 'application/json'
            },
            data:this.table.ids,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.$message.success(res.data.result);
                this.table.deleteConfirmDialogVisibel = false;
                this.searchCaseList();
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
        tableSelectionChange(value){
          this.table.multipleSelection = value;
          this.table.ids=[];
          for(let i=0;i<value.length;i++){
            this.table.ids.push(value[i].id);
          }
        },
        editCase(row){
          // 将操作状态设置为2：编辑
          this.operationType = 2;
          this.dialog.dialogVisible = true;
          // 获取case信息
          this.dialog.caseInfo = {...row};
        },
        getCaseStatus(bool){
          if (bool){
            return '成功'
          } else {
            return '失败'
          }
        },
        addVarialbe(){
          this.dialog.caseInfo.variableList.push({extractMethod:'',extractRule:'',variableName:''});
        },
        deleteVarialbe(index){
          this.dialog.caseInfo.variableList.splice(index,1);
        },
        addExpected(){
          this.dialog.caseInfo.expectedList.push({extractMethod:'',extractRule:'',compareType:'',expectedValue:''});
        },
        deleteExpected(index){
          this.dialog.caseInfo.expectedList.splice(index,1);
        },
        excuteSample(){
          let promise = new Promise((resolve,reject)=>{
            // 对现有数据进行更新
            this.$axios({
              method:"post",
              url: "/dubbo/update",
              headers:{
                'Content-type': 'application/json'
              },
              data:this.dialog.caseInfo,
            }).then(
              res=>{
                if (res.data.isSuccess===true){
                  this.searchCaseList();
                } else if(res.data.isSuccess===false){
                  this.$message.error(res.data.msg);
                }
                resolve(res);
              },err =>{
              });
          });
          promise.then((res)=>{
            this.dialog.responseParams.loading = true;
            this.$axios({
              method:"post",
              url: "/dubbo/excute",
              headers:{
                'Content-type': 'application/json'
              },
              data:[this.dialog.caseInfo.id],
            }).then(
              res=>{
                this.dialog.responseParams.loading = false;
                this.dialog.caseInfo = res.data.result;
                // 渲染执行按钮的状态
                this.refreshExcuteButtonStatus();
              },err =>{
                this.dialog.responseParams.loading = false;
              });
          });
        },
        refreshExcuteButtonStatus(){
          if (this.dialog.caseInfo.status){
            this.dialog.responseParams.excuteButtonStauts = 'success'
          } else {
            this.dialog.responseParams.excuteButtonStauts = 'danger'
          }
        }
      }
    }
</script>

<style scoped>

</style>



